import {
    Component,
    Input,
    OnInit,
    Output,
    EventEmitter,
    ViewChild,
    ElementRef,
    OnDestroy,
    AfterViewInit,
    SimpleChange,
    OnChanges,
    Inject,
    OpaqueToken,
    QueryList
} from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/merge';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/debounceTime';

import { RowHeightService } from './row-height.service';
import { ScrollerService, Action, PageAction, ScrollAction } from './scroller.service';
import { ColumnComponent } from './column.component';
import { DetailTemplateDirective } from './detail-template.directive';
import { isChanged } from './utils';
import { DetailsService } from './details.service';
import { ColumnsContainer } from './columns-container';

/**
 * @hidden
 */
const SCROLLER_FACTORY_TOKEN: OpaqueToken = new OpaqueToken('grid-scroll-service-factory');

/**
 * @hidden
 */
const DEFAULT_SCROLLER_FACTORY: any = observable => new ScrollerService(observable);

/**
 * @hidden
 */
const wheelDeltaY = (e) => {
    const deltaY = e.wheelDeltaY;

    if (e.wheelDelta && (deltaY === undefined || deltaY)) {
        return e.wheelDelta;
    } else if (e.detail && e.axis === e.VERTICAL_AXIS) {
        return (-e.detail) * 10;
    }

    return 0;
};

/**
 * @hidden
 */
@Component({
    providers: [
        {
            provide: SCROLLER_FACTORY_TOKEN,
            useValue: DEFAULT_SCROLLER_FACTORY
        }],
    selector: 'kendo-grid-list',
    template: `
    <div #lockedContainer class="k-grid-content-locked" [style.height.px]="height"
        *ngIf="lockedColumns.length" [style.width.px]="lockedWidth">
        <table [ngStyle]="style">
            <colgroup kendoGridColGroup
                [columns]="lockedColumns"
                [detailTemplate]="detailTemplate">
            </colgroup>
            <tbody kendoGridTableBody
                [data]="data"
                [columns]="lockedColumns"
                [detailTemplate]="detailTemplate"
                [skip]="skip"
                [selectable]="selectable">
            </tbody>
        </table>
        <div class="k-height-container">
            <div [style.height.px]="totalHeight"></div>
        </div>
    </div><div #container class="k-grid-content k-virtual-content"
        [style.height.px]="height"
        [kendoGridResizableContainer]="lockedColumns.length"
        [lockedWidth]="lockedWidth + 1">
        <table [ngStyle]="style" [style.width.px]="nonLockedWidth">
            <colgroup kendoGridColGroup
                [columns]="nonLockedColumns"
                [detailTemplate]="detailTemplate">
            </colgroup>
            <tbody kendoGridTableBody
                [data]="data"
                [columns]="nonLockedColumns"
                [detailTemplate]="detailTemplate"
                [skip]="skip"
                [selectable]="selectable">
            </tbody>
        </table>
        <div class="k-height-container">
            <div [style.height.px]="totalHeight"></div>
        </div>
    </div>`
})
export class ListComponent implements OnInit, OnDestroy, AfterViewInit, OnChanges {
    @Input() public data: Array<any>;
    @Input() public total: number;
    @Input() public height: number;
    @Input() public rowHeight: number;
    @Input() public detailRowHeight: number;
    @Input() public take: number;
    @Input() public skip: number = 0;
    @Input() public columns: ColumnsContainer = new ColumnsContainer(() => []);
    @Input() public detailTemplate: DetailTemplateDirective;
    @Input() public selectable: boolean;
    @Output() public pageChange: EventEmitter<Action> = new EventEmitter<Action>();
    @Output() public containerScroll: EventEmitter<{scrollTop: number, scrollLeft: number}> =
     new EventEmitter<{scrollTop: number, scrollLeft: number}>();

    public totalHeight: number;
    public style: any;

    @ViewChild("container") private container: ElementRef;
    @ViewChild("lockedContainer") private lockedContainer: ElementRef;

    private scroller: ScrollerService;
    private scrollSubscription: Subscription;
    private resizeSubscription: Subscription;
    private detailsSubscription: Subscription;
    private containerScrollSubscription: Subscription;
    private dispatcher: Subject<any> = new Subject<any>();
    private rowHeightService: RowHeightService;

    private PAGE_EVENT_TIMEOUT: number = 200;

    private get lockedColumns(): QueryList<ColumnComponent> {
        return this.columns.lockedColumns;
    }

    private get nonLockedColumns(): QueryList<ColumnComponent> {
        return this.columns.nonLockedColumns;
    }

    private get lockedWidth(): number {
        return this.lockedColumns.reduce((prev, curr) => prev + (curr.width || 0), 0);
    }

    private get nonLockedWidth(): number {
        if (this.lockedColumns.length) {
            return this.nonLockedColumns.reduce((prev, curr) => prev + (curr.width || 0), 0);
        }
        return undefined;
    }

    constructor(
        @Inject(SCROLLER_FACTORY_TOKEN) scrollerFactory: (x: Observable<any>) => ScrollerService,
        detailsService: DetailsService
    ) {
        this.scroller = scrollerFactory(this.dispatcher);
        this.detailsSubscription = detailsService.changes.subscribe(x => this.detailExpand(x));
    }

    public ngOnInit(): void {
        this.init();
    }

    public ngOnChanges(changes: {[propertyName: string]: SimpleChange}): void {
        if (isChanged("total", changes)) {
            this.init();
        }
    }

    public ngAfterViewInit(): void {

        this.container.nativeElement.scrollTop = this.rowHeightService.offset(this.skip);

        this.containerScrollSubscription = Observable
            .fromEvent(this.container.nativeElement, 'scroll')
            .map((event: any) => event.target)
            .do(this.onContainerScroll.bind(this))
            .subscribe(this.dispatcher);

        if (this.lockedContainer) {
            this.containerScrollSubscription = this.containerScrollSubscription
                .add(Observable.fromEvent(this.lockedContainer.nativeElement, 'mousewheel')
                    .merge(Observable.fromEvent(this.lockedContainer.nativeElement, 'DOMMouseScroll'))
                    .filter((event: any) => !event.ctrlKey)
                    .map((event: any) => wheelDeltaY(event))
                    .distinctUntilChanged()
                    .subscribe(x => this.container.nativeElement.scrollTop -= x)
                );
        }
    }

    public ngOnDestroy(): void {
        if (this.scroller) {
            this.scrollSubscription.unsubscribe();
            this.scroller.destroy();
        }

        if (this.containerScrollSubscription) {
            this.containerScrollSubscription.unsubscribe();
        }

        if (this.resizeSubscription) {
            this.resizeSubscription.unsubscribe();
        }

        if (this.detailsSubscription) {
            this.detailsSubscription.unsubscribe();
        }
    }

    public init(): void {
        this.rowHeightService = new RowHeightService(this.total, this.rowHeight, this.detailRowHeight);

        this.totalHeight = this.rowHeightService.totalHeight();

        const observable: Observable<Action> = this.scroller
            .create(this.rowHeightService, this.skip, this.take, this.total);

        this.scrollSubscription = observable
            .filter((x: Action) => x instanceof PageAction)
            .debounceTime(this.PAGE_EVENT_TIMEOUT)
            .subscribe(this.pageChange)
            .add(observable
                 .filter((x: Action) => x instanceof ScrollAction)
                 .subscribe(this.scroll.bind(this))
            );
    }

    protected detailExpand({ index, expand }: { index: number, dataItem: any, expand: boolean }): void {
        if (expand) {
            this.rowHeightService.expandDetail(index);
        } else {
            this.rowHeightService.collapseDetail(index);
        }
        this.totalHeight = this.rowHeightService.totalHeight();
    }

    private scroll({ offset }: ScrollAction): void {
        this.style = { transform: "translateY(" + offset + "px)" };
        //this.style = { top: offset + "px" };
    }

    private onContainerScroll({ scrollTop, scrollLeft }: { scrollTop: number, scrollLeft: number }): void {
        if (this.lockedContainer) {
            this.lockedContainer.nativeElement.scrollTop = scrollTop;
        }

        this.containerScroll.emit({ scrollTop, scrollLeft });
    }
}



